@import '../../settings/settings.scss';

.ConfirmEmailPage_section {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: $g_0;

  .ConfirmEmailPage_section_text1 {
    font-size: 3vw;
    font-weight: 600;
    text-align: center;

    color: $g_1;
  }

  .ConfirmEmailPage_section_steps {
    margin-top: 20px;

    color: $g_1;

    .ConfirmEmailPage_section_steps_el {
      color: $g_1;
      font-size: 1.3vw;
      margin-top: 10px;
      padding: 7px 25px;
      border-radius: 7px;
      background-color: $g_4;
    }
  }
}

@media screen and (max-width: $ADAPTIVE_S_1) {
  .ConfirmEmailPage_section {
    .ConfirmEmailPage_section_text1 {
      font-size: 6vw;
      font-weight: 600;
      text-align: center;
    }

    .ConfirmEmailPage_section_steps {
      margin-top: 20px;

      .ConfirmEmailPage_section_steps_el {
        font-size: 4vw;
        margin: 0px 30px;
        margin-top: 10px;
        padding: 7px 25px;
        border-radius: 7px;
        background-color: $g_4;
      }
    }
  }
}
