@import '../../settings/settings.scss';

// $title_font_size: 3.3vw;
// $subTitle_font_size: 0.8vw;
// $input_font_size: 0.8vw;
// $button_font_size: 0.8vw;
$title_font_size: 55px;
$subTitle_font_size: 16px;
$input_font_size: 16px;
$button_font_size: 16px;

$input_paddings: 65px;

$input_width: 400px;
$input_height: 70px;

//-------------------------
// mobile variables
$mob_title_font_size: 35px;
$mob_subTitle_font_size: 14px;
$mob_input_font_size: 14px;
$mob_button_font_size: 14px;

$mob_input_paddings: 55px;

$mob_input_width: 320px;
$mob_input_height: 60px;

// ================================

.RegistrationSection {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;

  .RegistrationSection_mainBlock {
    display: flex;
    justify-content: center;
    align-items: center;
    // background-color: aquamarine;
    height: 100%;
    // margin-top: $header_height;

    .RegistrationSection_mainBlock_wrapper {
      // background-color: pink;
      margin-top: -40px;
      display: flex;
      flex-direction: column;
      align-items: center;

      .RegistrationSection_mainBlock_wrapper_title {
        color: white;
        font-size: $title_font_size;
        font-weight: 700;
      }

      .RegistrationSection_mainBlock_wrapper_subTitle {
        display: flex;
        color: white;
        font-weight: 600;
        font-size: $subTitle_font_size;
        margin-top: 30px;

        .RegistrationSection_mainBlock_wrapper_subTitle_question {
          margin-right: 10px;
        }

        .RegistrationSection_mainBlock_wrapper_subTitle_linkToLogin {
          font-weight: 400;
          color: white;
        }
      }

      .eyeIconPassword {
        margin-top: 15px;
        cursor: pointer;
        font-size: 28px;
        color: white;
      }

      .RegistrationSection_mainBlock_wrapper_input_wrapper {
        $p1: &;

        display: flex;
        align-items: center;
        justify-content: flex-end;
        // background-color: pink;
        border-radius: 100px;
        margin-top: 15px;

        width: $input_width;
        height: $input_height;

        .RegistrationSection_mainBlock_wrapper_input {
          width: 100%;
          height: 100%;
          background-color: transparent;
          border: none;
          border-radius: 100px;
          border: 1px solid $g_0;
          padding: 0px $input_paddings;
          color: white;
          text-align: center;
          font-size: $input_font_size;
          font-weight: 600;
          transition: all 0.3s ease-in-out;

          &:focus {
            background-color: $g_0;
            color: $g_1;
            outline: none;
          }

          &::placeholder {
            text-align: center;
            color: white;
          }
        }

        .RegistrationSection_mainBlock_wrapper_againPassword_true {
          border: 1px dashed $r_0;
          color: $r_0;

          &:focus {
            background-color: $g_0;
            color: $g_1;
            outline: none;
          }
        }
      }

      .RegistrationSection_mainBlock_wrapper_createButton {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 15px;
        width: $input_width;
        height: $input_height;
        background-color: $g_0;
        border-radius: 100px;
        color: $g_1;
        font-size: $button_font_size;
        font-weight: 700;
        transition: all 0.3s ease-in-out;
        cursor: pointer;

        &:hover {
          background-color: $g_1;
          color: $g_0;
        }
      }

      .RegForms_Error {
        text-align: center;
        color: $r_0;
        font-size: 17px;
        font-weight: 600;
      }

      .LoginForms_Error_loging{
        text-align: center;
        color: $r_0;
        font-size: 17px;
        font-weight: 600;
      }
    }
  }
}
