@import '../../settings/settings.scss';

.statisticSection {
  height: 100vh;
  width: 100%;
  background-color: $g_0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: $header-height 0;
  // padding-top: $header-height;
  box-sizing: border-box;
}

.statisticContent {
  width: 1300px;
  height: 100%;
  display: flex;
  column-gap: 250px;
  justify-content: center;
}

.statisticBlockHeader {
  color: $g_1;
  font-size: 30px;
}

.statisticBigNumber {
  color: $g_1;
  font-size: 80px;
  font-weight: 600;
}



.cityName {
  color: $g_1;
  font-size: 16px;
}

.cityBlock {
  width: 100%;
  border-bottom: 1px solid $g_1;
  display: flex;
  justify-content: space-between;
}
.cityBlock_left {
  display: flex;
  flex-direction: column;
  // row-gap: 5px;

  .cityName {
    color: $g_1;
    font-size: 16px;
  }

  .cityPopulation {
    color: $g_1;
    font-size: 30px;
    font-weight: 600;
  }
}
.cityBlock_right {
  display: flex;
  align-items: flex-end;
  .cityBlock_emoji {
    font-size: 30px;
  }
}

.cityPopulation {
  font-size: 30px;
  font-weight: bold;
}

.statisticLeft,
.statisticRight {
  height: 100%;
  width: 30svw;
  // background-color: pink;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.statisticWrap {
  display: flex;
  flex-direction: column;
  row-gap: 16px;
}

@media (max-width: $ADAPTIVE_M_1) {
  .statisticContent {
    width: 100%;
    padding: 0 30px;
  }

  .statisticLeft,
  .statisticRight {
    width: 37vw;
    // background-color: rgb(140, 14, 35);

  }
}

@media (max-width: $ADAPTIVE_M_2) {
  .statisticContent {

    column-gap: 150px;

  }
  .statisticBlockHeader {
    font-size: 24px;
  }

  .statisticBigNumber {
    font-size: 60px;
  }

  .cityName {
    font-size: 12px;
  }

  .cityPopulation {
    font-size: 24px;
  }

  .statisticLeft,
  .statisticRight {
    width: 45vw;
    // background-color: rgb(104, 14, 140);

  }
}

@media (max-width: $ADAPTIVE_S_1) {
  .statisticContent {
    flex-direction: column;
    justify-content: flex-start;
    row-gap: 50px;
  }

  .statisticWrap {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .statisticBlockHeader {
    font-size: 18px;
    font-weight: bolder;
    width: 120px;
  }

  .statisticBigNumber {
    font-size: 36px;
    font-weight: bold;
  }

  .cityName {
    font-size: 16px;
    font-weight: normal;
  }

  .cityPopulation {
    font-size: 20px;
    font-weight: bold;
  }

  .statisticLeft,
  .statisticRight {
    height: 35%;
    width: 80vw;
  }
  .statisticWrapRightFirst {
    display: flex;
    flex-direction: column;
  }
  .statisticBlockHeader:last-child {
    width: 100%;
    font-size: 20px;
    font-weight: bolder;
  }
}
